import { Component } from '@angular/core';
import { NavController, NavParams, ToastController } from 'ionic-angular';

import * as WC from 'woocommerce-api';
var END_POINT: string = "http://wooshoes.azurewebsites.net";

import { ProductDetailsPage } from './../product-details/product-details';

@Component({
	selector: 'page-products-by-category',
	templateUrl: 'products-by-category.html',
})
export class ProductsByCategoryPage {
	WooCommerce: any;
	products: any[];
	page: number;
	category: any;

	constructor(
		private _navCtrl: NavController,
		private _toastCtrl: ToastController,
		private _navParams: NavParams
	) {
		this.page = 1;

		this.category = this._navParams.get('category');

		this.WooCommerce = WC({
			// -AZURE
			url: END_POINT,
			consumerKey: "ck_1543cce67a491ba996bdfd813634cb569a666448",
			consumerSecret: "cs_3234b5a879bec26ac111cb48a8b866424ef8d8bc"
		});

		this.WooCommerce.getAsync("products?filter[category]=" + this.category.slug).then((data) => {
			this.products = JSON.parse(data.body).products;
		}, (err) => {
			console.log(err)
		});
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad ProductsByCategoryPage');
	}

	loadMoreProducts(event) {
		this.page++;

		this.WooCommerce.getAsync("products?filter=" + this.category.slug + "&page=" + this.page).then((data) => {
			let tmp = (JSON.parse(data.body).products);

			this.products = this.products.concat(JSON.parse(data.body).products);

			event.complete();

			if (JSON.parse(data.body).products.length < 10) {
				event.enable(false);
				this._toastCtrl.create({
					message: "No more Products!",
					duration: 3000
				}).present();
			}

		}, (err) => {
			console.log(err)
		});
	}

	openProductPage(product) {
		this._navCtrl.push(ProductDetailsPage, { product });
	}
}



