import { LoginPage } from './../login/login';
import { Component } from '@angular/core';
import { NavController, NavParams, ViewController } from 'ionic-angular';
import { Storage } from '@ionic/storage';

import { CheckoutPage } from './../checkout/checkout';

@Component({
	selector: 'page-cart',
	templateUrl: 'cart.html',
})
export class CartPage {
	cartItem: any[] = [];
	total: any;
	showEmptyartMessage: boolean = false;

	constructor(
		private _navCtrl: NavController,
		private _navParams: NavParams,
		private _storage: Storage,
		private _viewCtrl: ViewController
	) {
		this.total = 0.0;
		this._storage.ready()
			.then(() => this._storage.get('cart'))
			.then(data => {
				this.cartItem = data;
				if (this.cartItem == null) return this.showEmptyartMessage = true;
				console.log(this.cartItem);
				if (this.cartItem.length > 0) {
					this.cartItem.forEach((item, index) => {
						this.total = this.total + (item.product.price * item.qty)
					});
				} else {
					this.showEmptyartMessage = true;
				}
			});
	}

	removeFromCart(item, i) {
		let price = item.product.price;
		let qty = item.product.qty;

		this.cartItem.splice(i, 1);

		this._storage.set('cart', this.cartItem)
			.then(() => {
				this.total = this.total - (price * qty);
			});

		if (this.cartItem.length == 0) {
			this.showEmptyartMessage = true;
		}

	}

	closeModal() {
		this._viewCtrl.dismiss();
	}

	checkout() {
		this._storage.get("userLoginInfo")
			.then(data => {
				if (data != null) {
					this._navCtrl.push(CheckoutPage);
				} else {
					this._navCtrl.push(LoginPage);
				}
			})
	}
}



