import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { OneSignal } from "@ionic-native/onesignal";

import { MenuPage } from './../pages/menu/menu';
import { SignupPage } from './../pages/signup/signup';

@Component({
	templateUrl: 'app.html'
})
export class MyApp {
	@ViewChild(Nav) nav: Nav;

	rootPage: any = MenuPage;

	constructor(
		private _platform: Platform,
		private _statusBar: StatusBar,
		private _splashScreen: SplashScreen,
		private _oneSignal: OneSignal
	) {
		this.initializeApp();
	}

	initializeApp() {
		this._platform.ready().then(() => {
			// Okay, so the platform is ready and our plugins are available.
			// Here you can do any higher level native things you might need.
			this._statusBar.styleDefault();
			this._splashScreen.hide();

			this._oneSignal.startInit('a532915c-5d37-4df8-a4ed-1e8e621c72f4', '648119072575');

			this._oneSignal.inFocusDisplaying(this._oneSignal.OSInFocusDisplayOption.InAppAlert);

			this._oneSignal.handleNotificationReceived().subscribe(() => {
				// do something when notification is received
			});

			this._oneSignal.handleNotificationOpened().subscribe(() => {
				// do something when a notification is opened
			});

			this._oneSignal.endInit();
		});
	}
}



