import { CartPage } from './../cart/cart';
import { Storage } from '@ionic/storage';
import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController } from 'ionic-angular';

import { HomePage } from './../home/home';
import { ProductsByCategoryPage } from './../products-by-category/products-by-category';
import { SignupPage } from './../signup/signup';
import { LoginPage } from './../login/login';

import * as WC from 'woocommerce-api'

var END_POINT: string = "http://wooshoes.azurewebsites.net";

@Component({
	selector: 'page-menu',
	templateUrl: 'menu.html',
})
export class MenuPage {

	homePage: any;
	WooCommerce: any;
	categories: any[];

	@ViewChild('content') childNavCtrl: NavController;
	user: any;
	loggedIn: boolean;

	constructor(
		private _navCtrl: NavController,
		private _navParams: NavParams,
		private _modalCtrl: ModalController,
		private _storage: Storage
	) {
		this.homePage = HomePage;
		this.categories = [];
		this.user = {};
		this.WooCommerce = WC({
			// -AZURE
			url: END_POINT,
			consumerKey: "ck_1543cce67a491ba996bdfd813634cb569a666448",
			consumerSecret: "cs_3234b5a879bec26ac111cb48a8b866424ef8d8bc"
			//-LOCAL
			// url: "http://localhost/woocommerce",
			// consumerKey: "ck_21db839fc315a9088df58078ae192114a6941267",
			// consumerSecret: "cs_16b932b654e0632c537164e064bb1f296efa5ead"
		});

		this.WooCommerce.getAsync("products/categories").then((data) => {
			// this.moreProducts = JSON.parse(data.body).products;
			let tem: any[] = JSON.parse(data.body).product_categories;

			for (var i = 0; i < tem.length; i++) {
				if (tem[i].parent == 0) {
					if (tem[i].slug == 'accessories') {
						tem[i].icon = 'images';
					}
					if (tem[i].slug == 'hoodies') {

						tem[i].icon = 'musical-notes';
					}
					if (tem[i].slug == 'tshirts') {
						tem[i].icon = 'shirt';

					}

					this.categories.push(tem[i]);
				}
			}

		}, (err) => {
			console.log(err)
		});
	}

	ionViewDidEnter() {
		this._storage.ready().then(() => {
			this._storage.get("userLoginInfo")
				.then(userLoginInfo => {
					if (userLoginInfo != null) {
						console.log("User logged in ...");
						this.user = userLoginInfo.user;
						console.log(this.user);
						this.loggedIn = true;
					} else {
						console.log("User not Found!");
						this.user = {};
						this.loggedIn = false;
					}
				})
		})
	}

	openCategoryPage(category) {
		this.childNavCtrl.setRoot(ProductsByCategoryPage, { category });
	}

	openPage(pageName: string) {
		if (pageName == 'signup') {
			this._navCtrl.push(SignupPage);
		}
		if (pageName == 'login') {
			this._navCtrl.push(LoginPage);
		}

		if (pageName == 'logout') {
			this._storage.remove('userLoginInfo')
				.then(() => {
					this.user = {};
					this.loggedIn = false;
				});
		}
		if (pageName == 'cart') {
			let modal = this._modalCtrl.create(CartPage);
			modal.present();
		}

	}
}



