import { Component, ViewChild } from '@angular/core';
import { NavController, Slides, ToastController } from 'ionic-angular';

import { ProductDetailsPage } from './../product-details/product-details';

import * as WC from 'woocommerce-api'

var END_POINT: string = "http://wooshoes.azurewebsites.net";

@Component({
	selector: 'page-home',
	templateUrl: 'home.html'
})
export class HomePage {
	@ViewChild('productSlides') productSlides: Slides;
	WooCommerce: any;
	products: any[];
	moreProducts: any[];
	page: number;

	constructor(
		private _navCtrl: NavController,
		private _toastCtrl: ToastController
	) {
		this.WooCommerce = WC({
			url: END_POINT,
			consumerKey: "ck_1543cce67a491ba996bdfd813634cb569a666448",
			consumerSecret: "cs_3234b5a879bec26ac111cb48a8b866424ef8d8bc"
		});

		this.page = 2;

		this.loadMoreProducts(null);

		this.WooCommerce.getAsync("products").then((data) => {
			this.moreProducts = JSON.parse(data.body).products;
		}, (err) => {
			console.log(err)
		});
	}

	ionViewDidLoad() {
		setInterval(() => {
			if (this.productSlides.getActiveIndex() === this.productSlides.length() - 1) {
				this.productSlides.slideTo(0);
			}

			this.productSlides.slideNext();

		}, 3000)
	}

	loadMoreProducts(event) {

		if (event == null) {
			this.page = 2;
			this.moreProducts = [];
		} else {
			this.page++;
		}

		this.WooCommerce.getAsync("products?page=" + this.page).then((data) => {
			this.products = this.moreProducts.concat(JSON.parse(data.body).products);
			if (event != null) {
				event.complete();
			}

			if (JSON.parse(data.body).products.length < 10) {
				event.enable(false);
				this._toastCtrl.create({
					message: "No more Products!",
					duration: 3000
				}).present();
			}

		}, (err) => {
			console.log(err)
		});
	}

	openProductPage(product) {
		this._navCtrl.push(ProductDetailsPage, { product });
	}
}



