import { CartPage } from './../cart/cart';
import { Component } from '@angular/core';
import { NavController, NavParams, ToastController, ModalController } from 'ionic-angular';
import { Storage } from "@ionic/storage";

import * as WC from 'woocommerce-api';
var END_POINT: string = "http://wooshoes.azurewebsites.net";

@Component({
	selector: 'page-product-details',
	templateUrl: 'product-details.html',
})
export class ProductDetailsPage {
	WooCommerce: any;
	reviews: any[] = [];
	product: any;
	constructor(
		private _navCtrl: NavController,
		private _navParams: NavParams,
		private _storage: Storage,
		private _toastCtrl: ToastController,
		private _modalCtrl: ModalController
	) {
		this.product = this._navParams.get('product');

		this.WooCommerce = WC({
			url: END_POINT,
			consumerKey: "ck_1543cce67a491ba996bdfd813634cb569a666448",
			consumerSecret: "cs_3234b5a879bec26ac111cb48a8b866424ef8d8bc"
		});

		this.WooCommerce.getAsync("products/" + this.product.id + "/reviews").then((data) => {
			this.reviews = JSON.parse(data.body).product_reviews;
			console.log(this.reviews);
		}, (err) => {
			console.log(err)
		});
	}

	addToCart(product) {

		this._storage.get("cart").then((data) => {

			if (data == null || data.length == 0) {
				data = [];

				data.push({
					"product": product,
					"qty": 1,
					"amount": parseFloat(product.price)
				})
			} else {

				let added = 0;

				for (let i = 0; i < data.length; i++) {

					if (product.id == data[i].product.id) {
						let qty = data[i].qty;

						console.log("Product is already in the cart");

						data[i].qty = qty + 1;
						data[i].amount = parseFloat(data[i].amount) + parseFloat(data[i].product.price);
						added = 1;
					}

				}

				if (added == 0) {
					data.push({
						"product": product,
						"qty": 1,
						"amount": parseFloat(product.price)
					})
				}

			}

			this._storage.set("cart", data).then(() => {
				console.log("Cart Updated");
				console.log(data);

				this._toastCtrl.create({
					message: "Cart Updated",
					duration: 3000
				}).present();

			})

		})

	}

	openCart() {
		let modal = this._modalCtrl.create(CartPage);
		modal.present();
	}

}



